#bgVideo {
  position: fixed;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100vh;
  object-fit: cover;
}

.video-overlay {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 220px;
  z-index: 9;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.0001) 0%, #000000 100%);
}
.main-heading {
  position: fixed;
  bottom: 0;
  z-index: 10000;
  text-align: center;
  right: 0;
  left: 0;
  padding-bottom: 2rem;
  h1 {
    font-family: "GTAmerica-ExtendedBlack";
    font-size: 2rem;
    letter-spacing: 0.6rem;
    text-transform: uppercase;
    @media only screen and (min-width: 768px) {
      font-size: 2.5rem;
    }
  }
  .lead {
    font-family: "GTAmerica-ExtendedMedium";
    text-align: center;
    @media only screen and (min-width: 768px) {
      max-width: 490px;
      letter-spacing: 1.22px;
    }
  }
}
